import axios from "axios";
import { METHOD_PICKUP } from "@/util/constants";

// Merchant info (Restaurant/Store), used in components for delivery selection and opening hours
export default {
  name: "merchant",
  data() {
    return {
      merchant: undefined,
      shipments: [],
    };
  },
  props: {
    // id: Number, ViewGroceries does not have id in props
    // so this function will alway return Nettó - Mjódd
    existingMerchant: Object,
  },
  computed: {
    availablePostCodes() {
      const { price_by_postcode: postCodeRaw } = this.merchant;
      let postCodes = postCodeRaw
        .split(",")
        .map(postCode => postCode.split(":")[0]);
      return postCodes;
    },
    supportPickupShipment() {
      return this.shipments.some(
        shipment => shipment.deal_address === METHOD_PICKUP
      );
    },
  },
  methods: {
    async getMerchantInfo() {
      if (!this.id) return;
      const params = new URLSearchParams();
      params.append("service", "seller_info");
      params.append("id", this.id);
      params.append("shipments", 1);
      const response = await axios.post("/ws.php", params);
      const merchant = response.data[0][0];
      this.merchant = merchant;
      this.shipments = Object.values(response.data[1]);
      return merchant;
    },
    checkValidPostalCode(userPostCode) {
      if (this.availablePostCodes.includes("*")) return true;
      return this.availablePostCodes.includes(userPostCode);
    },
  },
  watch: {
    async id() {
      await this.getMerchantInfo();
    },
  },
  async mounted() {
    this.merchant =
      this.merchant || this.existingMerchant || (await this.getMerchantInfo());
  },
};
