<template>
  <div class="draggable-scroll" ref="draggableScrollRef" @scroll="handleScroll">
    <slot />
  </div>
</template>

<script>
export default {
  name: "DraggableScroll",
  props: {
    handleScroll: {
      type: Function,
      default: () => {},
    },
  },
  methods: {
    dragScroll() {
      const slider = this.$refs.draggableScrollRef;
      let isDown = false;
      let startX;
      let scrollLeft;

      function onMouseDown(e) {
        isDown = true;
        startX = e.pageX - slider.offsetLeft;
        scrollLeft = slider.scrollLeft;
      }
      function onMouseLeave() {
        isDown = false;
      }
      function onMouseUp() {
        isDown = false;
      }
      function onMouseMove(e) {
        if (!isDown) return;
        e.preventDefault();
        const x = e.pageX - slider.offsetLeft;
        const walk = x - startX; //scroll-fast
        slider.scrollLeft = scrollLeft - walk;
      }

      function addEventListener() {
        slider.addEventListener("mousedown", onMouseDown);
        slider.addEventListener("mouseleave", onMouseLeave);
        slider.addEventListener("mouseup", onMouseUp);
        slider.addEventListener("mousemove", onMouseMove);
      }

      function removeEventListener() {
        slider.removeEventListener("mousedown", onMouseDown);
        slider.removeEventListener("mouseleave", onMouseLeave);
        slider.removeEventListener("mouseup", onMouseUp);
        slider.removeEventListener("mousemove", onMouseMove);
      }

      return {
        addEventListener,
        removeEventListener,
      };
    },
  },
  mounted() {
    this.dragControler = this.dragScroll();
    this.dragControler.addEventListener();
  },
  beforeDestroy() {
    this.dragControler.removeEventListener();
  },
};
</script>

<style lang="scss" scoped></style>
