<template>
  <div :class="['swipper-buttons-container', carouselArrowIndicatorClass]">
    <div
      class="swipper-buttons__arrow swipper-buttons__arrow--right"
      @click="handleSwipe({ direction: 'right' })"
    >
      <img src="@/assets/svg/arrow-right.svg" alt="arrow-right" />
    </div>
    <div
      class="swipper-buttons__arrow swipper-buttons__arrow--left"
      @click="handleSwipe({ direction: 'left' })"
    >
      <img src="@/assets/svg/arrow-left.svg" alt="arrow-left" />
    </div>

    <div class="swipper-buttons__slot-content" ref="carouselRef">
      <slot />
    </div>
  </div>
</template>

<script>
import get from "@/util/get";

export default {
  name: "SwipperButtons",
  props: {
    nextWidth: {
      type: Number,
      default: 195,
    },
    onSwipeClick: Function,
  },
  data() {
    return {
      carouselArrowIndicatorClass: "",
    };
  },
  computed: {
    carouselRef() {
      return get(this.$refs, "carouselRef.children")[0];
    },
  },
  methods: {
    updateCarouselArrowIndicator(
      { scrollLeft, scrollWidth, clientWidth } = this.carouselRef
    ) {
      const ableToScrollLeft = scrollLeft > 0;
      const ableToScrollRight = scrollLeft + clientWidth <= scrollWidth - 2;

      this.carouselArrowIndicatorClass = `${ableToScrollLeft &&
        "swipper-buttons--show-left-arrow"} ${ableToScrollRight &&
        "swipper-buttons--show-right-arrow"}`;
    },
    debouldUpdateCarouselArrowIndicator(clientData) {
      clearTimeout(this.updateCarouselArrowIndicatorTimer);
      this.updateCarouselArrowIndicatorTimer = setTimeout(
        () => this.updateCarouselArrowIndicator(clientData),
        100
      );
    },
    handleSwipe({ direction }) {
      let skipWidth = this.nextWidth;
      if (this.onSwipeClick) {
        skipWidth = this.onSwipeClick({ direction }).skipWidth;
      }
      this.carouselRef.scrollBy({
        left: skipWidth * (direction === "right" ? 1 : -1),
        behavior: "smooth",
      });
    },
  },
  mounted() {
    this.updateCarouselArrowIndicator(this.carouselRef);
    window.addEventListener(
      "resize",
      this.debouldUpdateCarouselArrowIndicator.bind(this, this.carouselRef)
    );
    this.carouselRef.addEventListener(
      "scroll",
      this.debouldUpdateCarouselArrowIndicator.bind(this, this.carouselRef)
    );
  },
  beforeDestroy() {
    window.removeEventListener(
      "resize",
      this.debouldUpdateCarouselArrowIndicator.bind(this, this.carouselRef)
    );
    this.carouselRef.removeEventListener(
      "scroll",
      this.debouldUpdateCarouselArrowIndicator.bind(this, this.carouselRef)
    );
  },
};
</script>

<style lang="scss" scoped>
.swipper-buttons-container {
  position: relative;
  z-index: 1;

  &.swipper-buttons--show-left-arrow .swipper-buttons__arrow--left,
  &.swipper-buttons--show-right-arrow .swipper-buttons__arrow--right {
    visibility: visible;
  }

  .swipper-buttons__arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border: 1px solid #dadada;
    width: 40px;
    height: 40px;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background-color: white;
    visibility: hidden;

    img {
      width: 20px;
      height: 20px;
    }
  }
  .swipper-buttons__slot-content {
    overflow: hidden;
  }

  .swipper-buttons__arrow--right {
    right: 15px;
  }

  .swipper-buttons__arrow--left {
    left: 15px;
  }
}
</style>
